<template>
  <div>
    <b-form @submit.prevent="registarUsuario()">
      <b-form-group label="Nombre Completo:" class="col-md-6 my-4">
        <b-form-input v-model="newUser.nombre_user" placeholder="Ingresa tu nombre completo"></b-form-input>
      </b-form-group>
      <b-form-group
        label="GameTag:"
        class="col-md-6"
        description="¿Como quieres ser reconocido en tu leyenda?"
      >
        <b-form-input v-model="newUser.usuario_user" placeholder="Ingresa un nametag valido"></b-form-input>
      </b-form-group>
      <b-form-group
        label="Correo electronico:"
        description="Nunca compartiremos tus datos con terceros."
        class="col-md-6 my-4"
      >
        <b-form-input v-model="newUser.email" required type="email" placeholder="Ingresa tu correo"></b-form-input>
      </b-form-group>

      <b-form-group label="Contraseña:" class="col-md-6">
        <b-form-input v-model="newUser.password" type="password" placeholder="Ingresa una contraseña"></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary" class="mx-2">Comenzar leyenda</b-button>
      <b-button type="reset" variant="danger" :to="{name: 'Home'}">Cancelar</b-button>
    </b-form>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      user: [],
      newUser: { nombre_user: "", usuario_user: "", password: "", email: "" }
    };
  },
  methods: {
    registarUsuario() {
      this.axios
        .post("/register", this.newUser)
        .then(res => {
          this.user.push(res.data);
          (this.user.nombre_user = ''),
            (this.user.usuario_user = ''),
            (this.user.password = ''),
            (this.user.email = '')
        })
        .catch(e => {
          console.log(e.response);
        });
    }
  }
};
</script>